<template>
  <div class="main-content">
    <!-- 列表页 -->
    <div v-if="showFlag">
      <el-form :inline="true" :model="searchForm" class="form-content">
                                            <el-form-item label="订单编号">
                <el-input v-model="searchForm.dingdanbianhao" 
                    placeholder="订单编号" clearable></el-input>
              </el-form-item>
                                                                  <el-form-item label="商品编号">
                <el-input v-model="searchForm.shangpinbianhao" 
                    placeholder="商品编号" clearable></el-input>
              </el-form-item>
                                                                  <el-form-item label="名称">
                <el-input v-model="searchForm.mingcheng" 
                    placeholder="名称" clearable></el-input>
              </el-form-item>
                                                                  <el-form-item label="类别">
                <el-input v-model="searchForm.leibie" 
                    placeholder="类别" clearable></el-input>
              </el-form-item>
                                                                                                                                                                                                                                                                                                  <el-form-item>
          <el-button round @click="search()">查询</el-button>
          <el-button
            v-if="isAuth('xiaoshoudingdan','新增')"
            type="primary"
            @click="addOrUpdateHandler()"
            round
          >新增</el-button>
          <el-button
            v-if="isAuth('xiaoshoudingdan','删除')"
            :disabled="dataListSelections.length <= 0"
            type="danger"
            @click="deleteHandler()"
            round
          >删除</el-button>
                    <el-button
            v-if="isAuth('xiaoshoudingdan','报表')"
            type="info"
            @click="chartDialog()"
            round
          >统计报表</el-button>
                  </el-form-item>
      </el-form>
      <div class="table-content">
        <el-table
            v-if="isAuth('xiaoshoudingdan','查看')"
            :data="dataList"
            border
            v-loading="dataListLoading"
            @selection-change="selectionChangeHandler"
            style="width: 100%;">
            <el-table-column
                type="selection"
                header-align="center"
                align="center"
                width="50">
            </el-table-column>
                                            <el-table-column
                    prop="dingdanbianhao"
                    header-align="center"
                    align="center"
                    sortable
                    label="订单编号">
                    <template slot-scope="scope">
                      {{scope.row.dingdanbianhao}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="shangpinbianhao"
                    header-align="center"
                    align="center"
                    sortable
                    label="商品编号">
                    <template slot-scope="scope">
                      {{scope.row.shangpinbianhao}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="mingcheng"
                    header-align="center"
                    align="center"
                    sortable
                    label="名称">
                    <template slot-scope="scope">
                      {{scope.row.mingcheng}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="leibie"
                    header-align="center"
                    align="center"
                    sortable
                    label="类别">
                    <template slot-scope="scope">
                      {{scope.row.leibie}}
                    </template>
                </el-table-column>
                                                              <el-table-column prop="tupian" 
                    header-align="center"
                    align="center" 
                    width="200" 
                    label="图片">
                    <template slot-scope="scope">
                      <div v-if="scope.row.tupian">
                        <img :src="scope.row.tupian.split(',')[0]" width="100" height="100">
                      </div>
                      <div v-else>无图片</div>
                    </template>
                  </el-table-column>
                                                            <el-table-column
                    prop="shuliang"
                    header-align="center"
                    align="center"
                    sortable
                    label="数量">
                    <template slot-scope="scope">
                      {{scope.row.shuliang}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="shoujia"
                    header-align="center"
                    align="center"
                    sortable
                    label="售价">
                    <template slot-scope="scope">
                      {{scope.row.shoujia}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="zongjiage"
                    header-align="center"
                    align="center"
                    sortable
                    label="总价格">
                    <template slot-scope="scope">
                      {{scope.row.zongjiage}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="dingdanriqi"
                    header-align="center"
                    align="center"
                    sortable
                    label="订单日期">
                    <template slot-scope="scope">
                      {{scope.row.dingdanriqi}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="kehumingcheng"
                    header-align="center"
                    align="center"
                    sortable
                    label="客户名称">
                    <template slot-scope="scope">
                      {{scope.row.kehumingcheng}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="dizhi"
                    header-align="center"
                    align="center"
                    sortable
                    label="地址">
                    <template slot-scope="scope">
                      {{scope.row.dizhi}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="lianxidianhua"
                    header-align="center"
                    align="center"
                    sortable
                    label="联系电话">
                    <template slot-scope="scope">
                      {{scope.row.lianxidianhua}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="beizhu"
                    header-align="center"
                    align="center"
                    sortable
                    label="备注">
                    <template slot-scope="scope">
                      {{scope.row.beizhu}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="gonghao"
                    header-align="center"
                    align="center"
                    sortable
                    label="工号">
                    <template slot-scope="scope">
                      {{scope.row.gonghao}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="yuangongxingming"
                    header-align="center"
                    align="center"
                    sortable
                    label="员工姓名">
                    <template slot-scope="scope">
                      {{scope.row.yuangongxingming}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="shouji"
                    header-align="center"
                    align="center"
                    sortable
                    label="手机">
                    <template slot-scope="scope">
                      {{scope.row.shouji}}
                    </template>
                </el-table-column>
                                                                                                                                              <el-table-column
                  prop="shhf"
                  header-align="center"
                  align="center"
                  sortable
                  label="审核回复">
              </el-table-column>
              <el-table-column
                  v-if="isAuth('xiaoshoudingdan','审核')"
                  prop="sfsh"
                  header-align="center"
                  align="center"
                  sortable
                  label="审核">
                  <template slot-scope="scope">
                    <span style="margin-right:10px">{{scope.row.sfsh=='是'?'通过':'未通过'}}</span>
                    <el-button  type="text" icon="el-icon-edit" size="small" @click="shDialog(scope.row)">审核</el-button>
                  </template>
              </el-table-column>
                        <el-table-column
                header-align="center"
                align="center"
                label="操作">
                <template slot-scope="scope">
                                <el-button v-if="isAuth('xiaoshoudingdan','查看')" type="text" icon="el-icon-edit" size="small" @click="addOrUpdateHandler(scope.row.id,'info')">详情</el-button>
                                                <el-button v-if="isAuth('xiaoshoudingdan','修改')" type="text" icon="el-icon-edit" size="small" @click="addOrUpdateHandler(scope.row.id)">修改</el-button>
                <el-button v-if="isAuth('xiaoshoudingdan','删除')" type="text" icon="el-icon-delete" size="small" @click="deleteHandler(scope.row.id)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
                                                                                                                                                                                                                                                                                                                <el-pagination
          @size-change="sizeChangeHandle"
          @current-change="currentChangeHandle"
          :current-page="pageIndex"
          :page-sizes="[10, 20, 50, 100]"
          :page-size="pageSize"
          :total="totalPage"
          layout="total, sizes, prev, pager, next, jumper"
          class="pagination-content"
        ></el-pagination>
      </div>
    </div>
    <!-- 添加/修改页面  将父组件的search方法传递给子组件-->
    <add-or-update v-if="addOrUpdateFlag" :parent="this" ref="addOrUpdate"></add-or-update>

    
        <el-dialog
      title="审核"
      :visible.sync="sfshVisiable"
      width="50%">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="审核状态">
          <el-select v-model="shForm.sfsh" placeholder="审核状态">
            <el-option label="通过" value="是"></el-option>
            <el-option label="不通过" value="否"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="内容">
          <el-input type="textarea" :rows="8" v-model="shForm.shhf"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="shDialog">取 消</el-button>
        <el-button type="primary" @click="shHandler">确 定</el-button>
      </span>
    </el-dialog>
    
        <el-dialog
      title="统计报表"
      :visible.sync="chartVisiable"
      width="800">  
                                                                                            <div id="shuliangChart" style="width:100%;height:600px;"></div>
                                                                                                                                                                                            <span slot="footer" class="dialog-footer">
        <el-button @click="chartDialog">返回</el-button>
      </span>
    </el-dialog>
      </div>
</template>
<script>
import AddOrUpdate from "./add-or-update";
export default {
  data() {
    return {
                                                                                                                                                                                                                                                                                                                                                                                                searchForm: {
        key: ""
      },
      dataList: [],
      pageIndex: 1,
      pageSize: 10,
      totalPage: 0,
      dataListLoading: false,
      dataListSelections: [],
      showFlag: true,
      sfshVisiable: false,
      shForm: {},
      chartVisiable: false,
      addOrUpdateFlag:false,
          };
  },
  mounted() {
    this.init();
    this.getDataList();
  },
  filters: {
    htmlfilter: function (val) {
      return val.replace(/<[^>]*>/g).replace(/undefined/g,'');
    }
  },
  components: {
    AddOrUpdate,
      },
  methods: {
                chartDialog() {
      this.chartVisiable = !this.chartVisiable;
      this.$nextTick(()=>{
                                                                                  // mingcheng mingcheng
                                                                                           //  shuliang
                                                                                                                                                                                                                                                                                                                                                     // xcolumn ycolumn mingcheng shuliang
                var shuliangChart = this.$echarts.init(document.getElementById("shuliangChart"),'macarons');
        this.$http({
            url: `value/xiaoshoudingdan/mingcheng/shuliang`,
            method: "get"
        }).then(({ data }) => {
            if (data && data.code === 0) {
                let res = data.data;
                let xAxis = [];
                let yAxis = [];
                let pArray = []
                for(let i=0;i<res.length;i++){
                    xAxis.push(res[i].mingcheng);
                    yAxis.push(res[i].total);
                    pArray.push({
                    value: res[i].total,
                    name: res[i].mingcheng
                    })
                    var option = {};
                                        option = {
                        tooltip: {
                          trigger: 'item',
                          formatter: '{b} : {c}'
                        },
                        title: {
                            text: '销售订单',
                            left: 'center'
                        },
                        xAxis: {
                            type: 'category',
                            data: xAxis
                        },
                        yAxis: {
                            type: 'value'
                        },
                        series: [{
                            data: yAxis,
                            type: 'bar'
                        }]
                    };
                                        // 使用刚指定的配置项和数据显示图表。
                    shuliangChart.setOption(option);
                    //根据窗口的大小变动图表
                    window.onresize = function() {
                        shuliangChart.resize();
                    };
                }
            }
        });
              })
    },
        init () {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            },
    search() {
      this.pageIndex = 1;
      this.getDataList();
    },
    // 获取数据列表
    getDataList() {
      this.dataListLoading = true;
      let params = {
        page: this.pageIndex,
        limit: this.pageSize,
        sort: 'id',
      }
                                          if(this.searchForm.dingdanbianhao!='' && this.searchForm.dingdanbianhao!=undefined){
            params['dingdanbianhao'] = '%' + this.searchForm.dingdanbianhao + '%'
          }
                                                            if(this.searchForm.shangpinbianhao!='' && this.searchForm.shangpinbianhao!=undefined){
            params['shangpinbianhao'] = '%' + this.searchForm.shangpinbianhao + '%'
          }
                                                            if(this.searchForm.mingcheng!='' && this.searchForm.mingcheng!=undefined){
            params['mingcheng'] = '%' + this.searchForm.mingcheng + '%'
          }
                                                            if(this.searchForm.leibie!='' && this.searchForm.leibie!=undefined){
            params['leibie'] = '%' + this.searchForm.leibie + '%'
          }
                                                                                                                                                                                                                                                                                                                                                  this.$http({
        url: "xiaoshoudingdan/page",
        method: "get",
        params: params
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.dataList = data.data.list;
          this.totalPage = data.data.total;
        } else {
          this.dataList = [];
          this.totalPage = 0;
        }
        this.dataListLoading = false;
      });
    },
    // 每页数
    sizeChangeHandle(val) {
      this.pageSize = val;
      this.pageIndex = 1;
      this.getDataList();
    },
    // 当前页
    currentChangeHandle(val) {
      this.pageIndex = val;
      this.getDataList();
    },
    // 多选
    selectionChangeHandler(val) {
      this.dataListSelections = val;
    },
    // 添加/修改
    addOrUpdateHandler(id,type) {
      this.showFlag = false;
      this.addOrUpdateFlag = true;
      this.crossAddOrUpdateFlag = false;
      if(type!='info'){
        type = 'else';
      }
      this.$nextTick(() => {
        this.$refs.addOrUpdate.init(id,type);
      });
    },
        // 审核窗口
    shDialog(row){
      this.sfshVisiable = !this.sfshVisiable;
      if(row){
        this.shForm = {
                    dingdanbianhao: row.dingdanbianhao,
                    shangpinbianhao: row.shangpinbianhao,
                    mingcheng: row.mingcheng,
                    leibie: row.leibie,
                    tupian: row.tupian,
                    shuliang: row.shuliang,
                    shoujia: row.shoujia,
                    zongjiage: row.zongjiage,
                    dingdanriqi: row.dingdanriqi,
                    kehumingcheng: row.kehumingcheng,
                    dizhi: row.dizhi,
                    lianxidianhua: row.lianxidianhua,
                    beizhu: row.beizhu,
                    gonghao: row.gonghao,
                    yuangongxingming: row.yuangongxingming,
                    shouji: row.shouji,
                    sfsh: row.sfsh,
                    shhf: row.shhf,
                    id: row.id
        }
      }
    },
    // 审核
    shHandler(){
      this.$confirm(`确定操作?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$http({
          url: "xiaoshoudingdan/update",
          method: "post",
          data: this.shForm
        }).then(({ data }) => {
          if (data && data.code === 0) {
            this.$message({
              message: "操作成功",
              type: "success",
              duration: 1500,
              onClose: () => {
                this.getDataList();
                this.shDialog()
              }
            });
          } else {
            this.$message.error(data.msg);
          }
        });
      });
    },
        // 下载
    download(file){
      window.open(`${file}`)
    },
    // 删除
    deleteHandler(id) {
      var ids = id
        ? [Number(id)]
        : this.dataListSelections.map(item => {
            return Number(item.id);
          });
      this.$confirm(`确定进行[${id ? "删除" : "批量删除"}]操作?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$http({
          url: "xiaoshoudingdan/delete",
          method: "post",
          data: ids
        }).then(({ data }) => {
          if (data && data.code === 0) {
            this.$message({
              message: "操作成功",
              type: "success",
              duration: 1500,
              onClose: () => {
                this.search();
              }
            });
          } else {
            this.$message.error(data.msg);
          }
        });
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
